@import "main";

:root {
    font-family: Verdana;
    text-de: en-US;
    margin: 0 0 0 0;
    text-align: center;
    --dark: #0072b1;
    --light: white;
    --tert: #003958;
    --shadow: #888888;
}

nav {
  display: flex;
  position: fixed;
  flex-direction: row;
  background-color: var(--dark);
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  width: 100vw;
  margin-top: 0px;
  font-weight: 900;
  z-index: 1000;
  top: 0px;
  left: 0px;
}

.navlink {
  color: var(--light);
  padding: 1rem;
  text-decoration: none;
  margin-top: 0px
}

a {
  text-decoration: none;
}

#main-doc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.project-tile {
  box-shadow: 2px 2px 2px 2px #888888;
  background-color: var(--light);
  padding-left: 2rem;
  padding-right: 2rem;
  justify-content: center;
}

.project-tile:hover, .scroll:hover {
  background-color: var(--dark);
  color: var(--light);
}

@media only screen and (max-width: 600px) {
  body {
    width: 100vw;
  }
}

#welcome-section, #projects, #contact {
  height: 100vh;
  padding-top: 10rem;
}

#show {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.scroll {
  background-color: var(--light);
  box-shadow: 2px 2px 2px 2px var(--shadow);
  border-style: none;
  padding: 1rem;
}

#welcome-section {
  background: url("https://images.unsplash.com/photo-1513530534585-c7b1394c6d51?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1502&q=80");
  right: 0px;
  left: 0px;
  width: 100vw;
}

#projects {
  background-color: var(--tert);
  width: 100vw;
  align-items: center;
}

#contact {
    background: var(--dark);
    width: 100vw;
}

footer {
  background-color: var(--dark);
  margin-bottom: 0px;
}

.light-text {
    color: var(--light);
}